import React , {Component} from "react";
import './Detail.css';


class Detail extends Component {
    constructor(props) {
      super(props);
      this.state = {
        error: null,
        isLoaded: false,
        events: []
      };
    }
  
    componentDidMount() {
      fetch("https://nqj8icqzf7.execute-api.ca-central-1.amazonaws.com/get")
        .then(res => res.json())
        .then(
          (result) => {
            this.setState({
              isLoaded: true,
              events: result.events,
            });
            console.log(result);
          },
          // Note: it's important to handle errors here
          // instead of a catch() block so that we don't swallow
          // exceptions from actual bugs in components.
          (error) => {
            this.setState({
              isLoaded: true,
              error
            });
          }
        )
    }
  
    
    }
  export default Detail